.cart-modal {
  border-radius: 4px;
  width: 580px;
  padding: var(--main-offset);

  .graph-modal__close {
    display: none;

    @include small-tablet {
      display: block;
    }
  }

  &__top {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: var(--main-offset);

    @include small-tablet {
      display: block;

    }
  }

  &__title {
    margin: 0;
    font: var(--prehigh-regular);
    font-weight: 700;
    color: var(--color-text);
  }

  &__order-number {
    font: var(--small-book);
    line-height: 14px;
    color: var(--color-grey);
  }
  &__form {
    display: block;

  }

  &__input {
    color: var(--color-grey);


    &:not(:last-child) {
      margin-bottom: var(--small-offset);
    }
  }
  @include small-tablet {
    width: 100%;
    max-width: 570px;

  }
}

.cart-modal-order {
  margin-bottom: var(--main-offset);
  border: 1px solid #d9d9d9;
  border-radius: 4px;
  padding: 30px;
  &__top {
    font: var(--middle-book);
    line-height: 14px;
    color: var(--color-grey);

    @include mobile {
      font-size: 14px;
    }

    span {
      font-weight: 700;
      color: var(--color-text);
      margin-left: var(--second-offset);
    }
  }

  &__list {
    &--visible {
      display: block;
    }
  }
  &__list {
    display: none;

    &--visible {
      display: block;

    }
  }

  &__quantity {
    margin-bottom: var(--second-offset);
  }

  &__summ {
    margin-bottom: var(--second-offset);
  }

  .mini-cart__item {
    &:not(:last-child) {
      margin-bottom: 14px;
    }
  }

  &__show {
    display: inline-flex;
    align-items: center;
    margin-bottom: var(--second-offset);
    color: var(--color-text);

    &::after {

      content: "+";
    }

    svg {
      margin-left: 5px;
      width: 7px;
      height: 7px;
      fill: currentColor;
      object-fit: cover;
      transition: transform 0.3s ease-in-out;

    }

    &--active {
      svg {
        transform: rotate(180deg);
      }
    }
  }
}
.cart-modal-order__content {

  .mini-product {
    padding: var(--small-offset) 0;
  }


}
